html {
  height: auto;
}

body {
  margin: 0;
  height: auto;
  font-family: Avenir Next;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: Avenir Next;
}

#root {
  height: auto;
}
.App {
  display: flex;
  text-align: center;
  background-color: #FFFFFF;
  min-height: 100%;
}

::-webkit-input-placeholder {
  font-family: Avenir Next;
  font-size: 14px;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: 0em;
  text-align: left;
  color: rgb(84, 95, 125);
}

.inputText label, ::placeholder {
  font-family: Avenir Next;
  font-size: 14px;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: 0em;
  text-align: left;
  color: rgb(84, 95, 125);
}
.logoDiv {
  display: flex;
  justify-content: flex-start;
}
.searchDiv {
  display: flex;
  height: 40px;
  width: 400px;
  padding-left: 10px;
  border-radius: 8px;
  border: 1px solid rgba(84, 95, 125, 0.15);
}

.searchInput {
  flex: 1 1;
}

.searchIconDiv {
  padding: 10px 20px;
  background: rgb(57, 205, 204);
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  cursor: pointer;
}

.menuItems {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #213F7D;
}

.flex-center {
  display: flex;
  flex-direction: column !important;
  justify-content: space-evenly;
  align-items: flex-start;
}

.MuiTabs-indicator {
  background-color: #39CDCC !important;
}

.menuActive {
  background-color: rgba(57, 205, 204, 0.06) !important;
  border-left: 3px solid #39CDCC !important;
}
.menuActive .MuiListItemText-root {
  opacity: 1 !important;
}
.menuActive .MuiListItemIcon-root {
  opacity: 1 !important;
  margin-left: 13px;
}

.dialogSelect .MuiInputBase-input {
  border-radius: 8px;
  position: relative;
  border: 1px solid #ced4da;
  font-size: 16;
  padding: 10px 12px;
  width: 100%;
}
.dialogSelect h6 {
  margin-top: 20px;
}

.dialogInput h6, .dialogSelect h6 {
  margin-top: 20px;
}
.dialogInput label, .dialogSelect label {
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
}
.dialogInput .MuiInputBase-root, .dialogSelect .MuiInputBase-root {
  margin-top: 5px;
}
.dialogInput .MuiInputBase-input, .dialogSelect .MuiInputBase-input {
  border-radius: 8px;
  position: relative;
  border: 1px solid #ced4da;
  font-size: 16;
  padding: 10px 12px;
}
@media screen and (max-width: 550px) {
  .Tab {
    max-width: 390px;
  }
}
@media screen and (min-width: 550px) and (max-width: 600px) {
  .Tab {
    max-width: 470px;
  }
}
@media screen and (min-width: 600px) and (max-width: 650px) {
  .Tab {
    max-width: 530px;
  }
}
@media screen and (min-width: 650px) and (max-width: 700px) {
  .Tab {
    max-width: 570px;
  }
}
@media screen and (min-width: 700px) and (max-width: 750px) {
  .Tab {
    max-width: 600px;
  }
}
@media screen and (min-width: 900px) and (max-width: 1000px) {
  .Tab {
    max-width: 500px;
  }
}
